  background-color:var(--background-color);
  background-image:linear-gradient(#444cf7 1px, transparent 1px),linear-gradient(to right, #444cf7 1px, #e5e5f7 1px);
  background-image:linear-gradient(var(--main) 1px, transparent 1px),linear-gradient(to right, var(--main) 1px, var(--background-color) 1px);
  background-size:20px 20px
}

.skew-dot-bg{
  background-color:#e5e5f7;
  background-color:var(--background-color);
  background-image:radial-gradient(#444cf7 3px, transparent 3px),radial-gradient(#444cf7 3px, #e5e5f7 3px);
  background-image:radial-gradient(var(--main) 3px, transparent 3px),radial-gradient(var(--main) 3px, var(--background-color) 3px);
  background-size:30px 30px;
  background-position:0 0,15px 15px
}

.skew-dot-transparent-bg{
  background-color:transparent;
  background-image:radial-gradient(transparent 3px, transparent 3px),radial-gradient(transparent 3px, #e5e5f7 3px);
  background-image:radial-gradient(transparent 3px, transparent 3px),radial-gradient(transparent 3px, var(--background-color) 3px);
  background-size:30px 30px;
  background-position:0 0,15px 15px
}

.cross-bg{
  background-color:#e5e5f7;
  background-color:var(--background-color);
  background:radial-gradient(circle, transparent 20%, #e5e5f7 20%, #e5e5f7 80%, transparent 80%, transparent),radial-gradient(circle, transparent 20%, #e5e5f7 20%, #e5e5f7 80%, transparent 80%, transparent) 25px 25px,linear-gradient(#444cf7 2px, transparent 2px) 0 -1px,linear-gradient(90deg, #444cf7 2px, #e5e5f7 2px) -1px 0;
  background:radial-gradient(circle, transparent 20%, var(--background-color) 20%, var(--background-color) 80%, transparent 80%, transparent),radial-gradient(circle, transparent 20%, var(--background-color) 20%, var(--background-color) 80%, transparent 80%, transparent) 25px 25px,linear-gradient(var(--main) 2px, transparent 2px) 0 -1px,linear-gradient(90deg, var(--main) 2px, var(--background-color) 2px) -1px 0;
  background-size:50px 50px, 50px 50px, 25px 25px, 25px 25px
}

.paper-bg{
  background-color:#e5e5f7;
  background-color:var(--background-color);
  background-image:linear-gradient(#444cf7 2px, transparent 2px),linear-gradient(90deg, #444cf7 2px, transparent 2px),linear-gradient(#444cf7 1px, transparent 1px),linear-gradient(90deg, #444cf7 1px, #e5e5f7 1px);
  background-image:linear-gradient(var(--main) 2px, transparent 2px),linear-gradient(90deg, var(--main) 2px, transparent 2px),linear-gradient(var(--main) 1px, transparent 1px),linear-gradient(90deg, var(--main) 1px, var(--background-color) 1px);
  background-size:50px 50px, 50px 50px, 25px 25px, 25px 25px;
  background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px
}

.diagonal-bg{
  background-color:#e5e5f7;
  background-color:var(--background-color);
  background-size:25px 25px;
  background-image:repeating-linear-gradient(45deg, #444cf7 0, #444cf7 2px, #e5e5f7 0, #e5e5f7 50%);
  background-image:repeating-linear-gradient(45deg, var(--main) 0, var(--main) 2px, var(--background-color) 0, var(--background-color) 50%)
}

.radial-bg{
  background:repeating-radial-gradient(#444cf7, #444cf7 5px, #e5e5f7 5px, #e5e5f7 10px);
  background:repeating-radial-gradient(var(--main), var(--main) 5px, var(--background-color) 5px, var(--background-color) 10px)
}

.radial-bg2{
  background:repeating-radial-gradient(circle, #e5e5f7, #e5e5f7 20px, #444cf7 20px, #444cf7 40px);
  background:repeating-radial-gradient(circle, var(--background-color), var(--background-color) 20px, var(--main) 20px, var(--main) 40px)
}

.half-transparent-bg{
  background:rgba(0,0,0,0.85)
}

::-webkit-input-placeholder{
  color:#1343e6 !important;
  color:var(--blue) !important
}

:-moz-placeholder{
  color:#1343e6 !important;
  color:var(--blue) !important
}

input:focus::-webkit-input-placeholder{
  color:transparent !important
}

footer ul .link{
  display:inline-block;
  vertical-align:middle;
  -webkit-transform:perspective(1px) translateZ(0);
  transform:perspective(1px) translateZ(0);
  box-shadow:none;
  position:relative;
  overflow:hidden;
  padding-bottom:5px
}

footer ul .link:after{
  content:"";
  position:absolute;
  z-index:-1;
  left:100%;
  right:0;
  bottom:0;
  background:#7a4ef4;
  background:var(--purple);
  height:2px;
  transition-property:left;
  transition-duration:0.3s;
  transition-timing-function:ease-out
}

footer ul .link:hover:after{
  left:0
}

@-webkit-keyframes rotation{
  from{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  to{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

@keyframes rotation{
  from{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  to{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

.grid-wrapper{
  display:grid;
  grid-template-rows:repeat(4, 1fr);
  grid-auto-flow:column;
  grid-gap:10px;
  gap:10px
}

.bang{
  display:block;
  top:calc(100vh - 170px);
  right:80px
}

.cannon{
  -webkit-transform:rotate(130deg);
  transform:rotate(130deg);
  right:0;
  top:calc(100vh - 150px);
  width:120px
}

.logo{
  left:0;
  top:0
}

.logo>img{
  width:80px
}

iframe{
  height:calc(100vh - 4px);
  width:calc(100vw - 4px);
  box-sizing:border-box
}

@-webkit-keyframes scaleIn{
  from{
    -webkit-transform:scale(0);
    transform:scale(0)
  }

  to{
    -webkit-transform:scale(1);
    transform:scale(1)
  }
}

@keyframes scaleIn{
  from{
    -webkit-transform:scale(0);
    transform:scale(0)
  }

  to{
    -webkit-transform:scale(1);
    transform:scale(1)
  }
}

@-webkit-keyframes fadeIn{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

@keyframes fadeIn{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

