.credit-logo{
  background-repeat:no-repeat;
  width:100%;
  height:100%;
  position:absolute;
  left:0;
  top:0;
  background-size:auto 40px;
  background-position:20px 20px
}

.main-left{
  padding:40px;
  border:1px solid;
  boxShadow:-10px 10px blue;
  background-color:#fdee21;
  color:blue;
  width:100%
}

.stepper-dot-container{
  transition:all .8s easeOut
}

.stepper-dot-container .stepper-dot{
  width:8px;
  height:8px;
  border-radius:50%;
  background-color:#e0e6ed;
  background-color:var(--gray-light)
}

.stepper-dot-container svg{
  display:none;
  fill:currentColor;
  width:1em;
  height:1em;
  font-size:1.5rem;
  transition:fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  -webkit-flex-shrink:0;
          flex-shrink:0;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
  color:#00f4b7;
  color:var(--green);
  z-index:1;
  font-size:24px
}

.stepper-dot-container.active .stepper-dot{
  background-color:#00f4b7;
  background-color:var(--green);
  width:12px;
  height:12px
}

.stepper-dot-container.completed .stepper-dot{
  display:none
}

.stepper-dot-container.completed svg{
  display:inline-block
}

.stepper-dot-container.completed.active svg{
  border:1px solid;
  border-radius:50%
}

.stepper-line-container{
  top:10px;
  left:calc(-50% + 16px);
  right:calc(50% + 16px)
}

.stepper-line{
  border-color:#e0e6ed;
  border-color:var(--gray-light);
  border-radius:1px;
  border-top-width:3px;
  border-top-style:solid
}

.stepper-line.completed{
  border-color:#00f4b7;
  border-color:var(--green)
}

.ring-mouse{
  position:fixed;
  top:-30px;
  left:-30px;
  width:30px;
  height:30px;
  border:3px solid var(--blue);
  border-radius:100%;
  -webkit-transform:translate(-50%, -50%);
  transform:translate(-50%, -50%);
  transition-duration:100ms;
  transition-timing-function:easeOut;
  will-change:width, height, transform, border;
  z-index:999;
  pointer-events:none;
  transition:all .2s ease
}

.dot{
  position:fixed;
  top:50%;
  left:50%;
  width:8px;
  height:8px;
  background-color:blue;
  border-radius:100%;
  -webkit-transform:translate(-50%, -50%);
  transform:translate(-50%, -50%);
  z-index:999;
  pointer-events:none;
  display:none
}

.ring-mouse.hovered{
  width:500px;
  height:500px;
  border-width:3px;
  border-color:blue
}

.ring-mouse.true-clicked{
  width:300px;
  height:300px
}

.dot.hovered{
  display:none
}

.arrow-cursor{
  display:none
}

.arrow-cursor,.smile-cursor{
  width:150px;
  height:110px;
  position:fixed;
  left:-150px;
  top:-110px;
  z-index:10000000;
  pointer-events:none
}

.arrow-cursor__icon,.smile-cursor__icon{
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height:100%
}

.arrow-cursor__path,.smile-cursor__path{
  fill:none;
  stroke:#000;
  stroke-linecap:round;
  stroke-linejoin:round;
  stroke-width:1.5px
}

